<template>
  <div class="card">
    <month-select v-if="showMonth" @change="onMonthChange" />
    <a-spin v-if="spinning" :spinning="spinning" />
    <slot v-else></slot>
  </div>
</template>

<script>
import MonthSelect from "./MonthSelect.vue";

export default {
  name: "card",
  data() {
    return {
      spinning: true,
      showMonth: false
    };
  },
  watch: {
    locking(locking) {
      if (!locking) {
        this.spinning = false;
      }
    }
  },
  computed: {
    locking() {
      return this.$store.state.lock.lockingList.some(
        ({ id }) => id === this._uid
      );
    }
  },
  beforeCreate() {
    this.$lock();
  },
  components: {
    MonthSelect
  },
  methods: {
    onMonthChange(month) {
      console.log(month);
    }
  }
};
</script>

<style lang="less" scoped>
.card {
  position: relative;
  overflow: hidden;

  &::before {
    position: absolute;
    width: 100%;
    height: 100%;

    content: "";
    background: linear-gradient(to left, #00beff, #00beff) left top no-repeat,
      linear-gradient(to bottom, #00beff, #00beff) left top no-repeat,
      linear-gradient(to left, #00beff, #00beff) right top no-repeat,
      linear-gradient(to bottom, #00beff, #00beff) right top no-repeat,
      linear-gradient(to left, #00beff, #00beff) left bottom no-repeat,
      linear-gradient(to bottom, #00beff, #00beff) left bottom no-repeat,
      linear-gradient(to left, #00beff, #00beff) right bottom no-repeat,
      linear-gradient(to left, #00beff, #00beff) right bottom no-repeat;
    background-size: 2px 20px, 20px 2px;
  }

  background: rgba(40, 115, 184, 0.1);
}
</style>

<style >
.card .ant-spin {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.card .ant-spin .loading {
  font-size: 48px !important;
}
</style>