<template>
  <a-date-picker
    class="month-select"
    :open="open"
    :mode="'month'"
    :inputReadOnly="true"
    :align="{offset: [10, 24]}"
    @panelChange="onChange"
  >
    <div class="month-text" @click="open = !open">
      {{ time ? dateText : '日期选择'}}
      <a-icon type="down" />
    </div>
  </a-date-picker>
</template>
<script>
export default {
  data() {
    return {
      open: false,
      time: undefined
    };
  },
  computed: {
    dateText() {
      let time = this.time;
      return [time.year(), `0${time.month() + 1}`.slice(-2)].join("-");
    }
  },
  methods: {
    onChange(value) {
      this.time = value;
      this.open = false;

      this.$emit("change", this.dateText);
    }
  }
};
</script>
<style scoped>
.month-select {
  position: absolute;
  color: #00d9d5;
  font-size: 10px;
  left: 0;
  top: 0;
  z-index: 2;
  cursor: pointer;
}

.month-text {
  padding: 8px 12px;
}
</style>