<template>
  <div style="width:100%;height:100%;" v-show="!spinning" ref="container"></div>
</template>

<script>
import echarts from "echarts";

export default {
  name: "echart",
  data() {
    return {
      spinning: false,
      chart: undefined
    };
  },
  props: ["option"],
  watch: {
    option: {
      handler(opt) {
        this.chart.setOption(opt);
      },
      deep: true
    }
  },
  mounted() {
    let option = this.option || {};

    let container = this.$refs.container;

    let chart = echarts.init(container, "yunchengly");

    this.$emit("ready", { echarts, chart });

    chart.setOption(option);

    this.chart = chart;
  }
};
</script>
